* {
  font-family: Roboto;
  font-size: 15px;
  line-height: 16px;
}

@media screen and (max-width: 767px) {
  * {
    font-family: Roboto;
    font-size: 12px;
    font-weight: 500;
    line-height: 16px;
  }

  .header-container {
    width: 100%;
    height: 230px;
    background-image: url("FondoJPG.501ad1fa.jpg");
    margin: 0 auto;
  }

  .icon {
    fill: #fff;
    position: relative;
    top: 5px;
  }

  .nav-main {
    justify-content: space-between;
    display: flex;
  }

  .logo-name {
    align-items: baseline;
    margin-top: 48px;
    margin-left: 23px;
    display: flex;
  }

  .nav-options {
    margin-top: 48px;
    margin-right: 20px;
  }

  .list-nav {
    gap: 39px;
    list-style: none;
    display: flex;
  }

  .list-nav a {
    color: #fff;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: 14px;
  }

  .search-o {
    justify-content: center;
    margin-top: 38px;
    margin-left: 21px;
    display: flex;
  }

  .logo {
    display: none;
  }

  .footer-container {
    height: 100px;
    background-color: #f7f7f7;
    flex-direction: column;
    align-items: center;
    display: flex;
  }

  .footer-mobile {
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 10px;
    display: flex;
  }

  .fotter-top-messa {
    margin-top: 28.7px;
    margin-bottom: 10px;
  }

  .fotter-top-messa p, .footer-bottom p {
    margin-top: 0;
    margin-bottom: 0;
  }

  .nav-underline a:after {
    content: "";
    width: 100%;
    height: 4px;
    opacity: 0;
    background-color: #ff6b08;
    margin-top: 1px;
    position: absolute;
    top: 100%;
    left: 0;
  }

  .nav-underline a {
    text-decoration: none;
    position: relative;
  }

  .nav-underline a:hover:after, .nav-underline a.selected:after {
    opacity: 1;
  }

  .searcher {
    width: 336px;
    height: 24px;
    color: #fff;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    border-bottom: .5px solid #fff;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 16px;
  }

  .searcher:focus {
    color: #fff;
    outline: none;
  }

  .searcher::-ms-input-placeholder {
    color: #fff;
  }

  .searcher::placeholder {
    color: #fff;
  }

  .icon-search {
    height: 15px;
    position: absolute;
    top: 3.5px;
    right: 2px;
  }

  .button-search {
    width: 23px;
    height: 23px;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    position: relative;
    top: -3.5px;
    right: 26px;
  }

  div#form-container {
    position: relative;
    top: -25px;
  }

  .button__container {
    width: 340px;
    height: 60px;
    border-color: #00f;
    justify-content: center;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    display: flex;
  }

  .library__Button {
    width: 200px;
    height: 50px;
    color: #fff;
    background-color: rgba(0, 0, 0, 0);
    border: 1px solid #fff;
    border-radius: 5px;
    margin: 7.5px;
  }

  .library__Butto {
    width: 200px;
    height: 20px;
    color: #fff;
    background-color: rgba(0, 0, 0, 0);
    border: 1px solid #fff;
    border-radius: 5px;
    margin: 7.5px;
  }

  .library__Butto:hover, .library__Button:hover {
    cursor: pointer;
    background-color: #ff6b08;
    border-style: none;
  }

  .library__Button:focus {
    background-color: #ff6b08;
    border-style: none;
  }

  .no-content {
    text-align: center;
    font-size: 25px;
  }
}

