body {
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1200px;
  margin: 60px auto 0;
  padding: 0 20px;
}

.containery {
  color: #333;
  background-color: #ff6b01;
}

h1 {
  text-align: center;
  margin-top: 50px;
  margin-bottom: 20px;
  font-size: 2.5rem;
}

.peliculas {
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}

.pelicula {
  max-width: 370px;
  cursor: pointer;
  border-radius: 15px;
  margin-bottom: 20px;
  transition: all .3s ease-in-out;
}

.pelicula:hover {
  box-shadow: 0 0 20px rgba(0, 0, 0, .4);
}

@media only screen and (min-width: 768px) {
  .pelicula {
    width: calc(50% - 10px);
  }
}

@media only screen and (min-width: 1024px) {
  .pelicula {
    width: calc(33.33% - 10px);
  }
}

.pelicula__imagen {
  max-width: 100%;
  height: 556px;
  border-radius: 5px;
  display: block;
}

.pelicula__titulo {
  margin: 10px 0 0;
  padding: 10px;
  font-size: 1.5rem;
  line-height: 25px;
}

.pelicula__estreno {
  color: #ff6b08;
  margin: 0;
  padding: 10px;
  font-family: Roboto;
  font-size: 1.5rem;
  font-style: normal;
}

.pelicula__genero {
  color: #ff6b08;
  margin: 0;
  padding: 10px;
  font-family: Roboto;
  font-size: 1rem;
  font-style: normal;
}

.contenedor-paginacion {
  justify-content: center;
  margin-top: 30px;
  margin-bottom: 15px;
  display: flex;
}

.pagina {
  color: #333;
  cursor: pointer;
  background-color: #e0e0e0;
  border: none;
  border-radius: 4px;
  margin: 0 4px;
  padding: 8px 16px;
  font-size: 16px;
  transition: background-color .3s;
}

.pagina:hover {
  background-color: #ccc;
}

.pagina-actual {
  color: #fff;
  background-color: #ff6b08;
}

.modalContainer {
  z-index: 2;
  width: 82%;
  padding-top: 0;
  display: none;
  position: fixed;
  top: 3%;
  left: 9%;
  overflow: auto;
}

.movie_title {
  text-transform: uppercase;
  margin: 0% 0% 2%;
  font-size: 30px;
  line-height: 30px;
}

.modalContainer .modal-content {
  width: 80%;
  background-color: #fff;
  border: 1px solid purple;
  margin: auto;
}

.modalContainer .button_close {
  color: #040616;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(0, 0, 0, 0);
  margin: 2% 3% 0%;
  font-size: 18px;
  position: fixed;
  top: 3%;
  right: 15%;
}

.modalContainer .button_close:hover {
  color: #ff6b01;
  cursor: pointer;
  text-decoration: none;
}

.data_info {
  margin: 0%;
  padding: 4% 2%;
}

.data_info:hover {
  color: #fff;
  background-color: #ff6b01;
  border-radius: 5px;
}

.data_movie {
  padding-top: 2%;
  display: flex;
}

.data_key {
  width: 40%;
  color: #8c8c8c;
}

.data_value {
  width: 60%;
}

.data_sinopsis {
  padding: 2% 0% 5%;
}

.sub-content {
  padding: 5% 3%;
  display: flex;
}

.sub-content_data {
  width: 50%;
}

.movie_img {
  width: 90%;
  border-radius: 10px;
}

.add-button {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 5px;
  margin-right: 3%;
  padding: 4%;
}

.add-button:hover {
  color: #fff;
  background-color: #ff6b01;
  border: 1px solid #fff;
}

@media screen and (min-width: 200px) and (max-width: 1023px) {
  .modalContainer .modal-content {
    z-index: 2;
    width: 82%;
    padding-top: 0;
    display: none;
    position: fixed;
    top: 3%;
    left: 9%;
    overflow: auto;
  }

  .movie_title {
    text-transform: uppercase;
    margin: 0% 0% 2%;
    font-size: 30px;
    line-height: 25px;
  }

  .modalContainer .button_close {
    margin: 2% 2% 0%;
    padding: 0%;
    right: 15%;
  }

  .sub-content {
    padding: 8% 4%;
  }
}

@media screen and (max-width: 767px) {
  .sub-content {
    flex-direction: column;
    padding: 13% 3%;
  }

  .sub-content_data {
    width: 100%;
  }

  .movie_img {
    width: 100%;
    padding-bottom: 10%;
  }

  .movie_title {
    font-size: 20px;
  }

  .add_button {
    width: 46%;
  }
}

